import React from 'react'

function CollapsibleArrowUp(props) {
    return (
        <svg className={props.className || "upArrow" } name={props?.name} stepIndex={props?.stepIndex} onClick={props?.onClick} xmlns="http://www.w3.org/2000/svg" width="12" height="9" viewBox="0 0 12 9">
        <defs>
        </defs>
            <path data-name="Path 6 Copy" d="M0,0,5,5,0,10" transform="translate(1 7) rotate(-90)" fill="none" stroke={props.color||"#202020"} strokeLinecap="round" 
            strokeLinejoin="round" strokeMiterlimit="10" strokeWidth="1"/>
        </svg>

    )
}

export default CollapsibleArrowUp
