import {makeStyles} from '@material-ui/core/styles';
import ElemDeleteControl from "../ElemDeleteControlWrapper"
import {checkValid,isObjectEmpty} from "../../../Resources/globalFunctions";
import {BlueButton, DullButton, LitButton} from "../../../ReusableComponents/GetButtons/GetButtons";
import CircularProgress from '@material-ui/core/CircularProgress';
import DownArrow from "../../../Icons/DownArrow"
import TickCircle from "../../../Icons/TickCircle";
import UpArrow from "../../../Icons/UpArrow"
import styles2 from '../../css/COLUMN.module.css'
import "./wizard.css"
import ELEMENTS from '../ELEMENTS'
import QuickTools from '../../../ReusableComponents/QuickTools/QuickTools';
import axios from "../../../axios";
import CustomizedAlert from '../../../ReusableComponents/Alert/CustomizedAlert';
import { changeControlProperty, createNewId, makeChildrenEmpty, setMandatoryControls } from "../../../components/IFormDesignerLayout/IFormDesignerLogic";
import {useDispatch,updateStore} from "../../../Redux"
import MuiAlert from "@material-ui/lab/Alert";
import Snackbar from "@material-ui/core/Snackbar";
import { data } from "jquery";
import {functions, renderExecuteServerEventAPIData} from "../../../components/IFormDesignerLayout/ApplicationAPI/applicationApi.js";
import { handleDisplayProperty } from "../../../components/IFormDesignerLayout/IFormDesignerLogic";
