    function handleStep(e){
        if(props.previewmode===false || ((activeStepIndex > (e.target.getAttribute("stepIndex") || e.currentTarget.getAttribute("stepIndex")) || window.isComponentValidated("wizardSubform",dataArray,true)===true) && props.previewmode===true) ){
            e.stopPropagation();
            if(props.previewmode!== true){

            setQuickToolsClose(true, () => {

                setTimeout(() => {
                
                  setQuickToolsClose(false);
                
                }, 500);
            
              });  
            }

            if(style.navigationMethod==="viaBoth"){
            const stepId=e.target.getAttribute("name") || e.currentTarget.getAttribute("name");
            const stepIndex=e.target.getAttribute("stepIndex") || e.currentTarget.getAttribute("stepIndex");

            setActiveStep(stepId);        
            setActiveStepIndex(stepIndex);
            formState2.isStep={"parentStep":stepIndex};

                if(stepId!==activeStep){
            const firstSubStepIndex=filteredData?.filter((step) => step.id === stepId).shift().children?.findIndex(function(substep){return substep?.elem_type?.toLowerCase()==="substep"});
            
            if(firstSubStepIndex!==-1){
                setActiveSubStep(filteredData?.filter((step) => step.id === stepId).shift().children?.[firstSubStepIndex].id);
                setActiveSubStepIndex(firstSubStepIndex.toString());
                setWizardIndexes({
                    stepIndex: stepIndex,
                    subStepIndex: firstSubStepIndex.toString()
                })
            } 
            
            else {
                setActiveSubStep("");
                setActiveSubStepIndex("");
                setWizardIndexes({
                    stepIndex: stepIndex,
                    subStepIndex: ""
                })
            }   
                }
                if(containsSubsteps(filteredData?.[stepIndex],"substep")) {
                    setArrow("down");
                }
            }

            if(window.isPortalLive)
                setMandatoryControls([])
            props.handleSelected(e,props.id,"n")
        }
    }
    
